'use client';

import { useState } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';

const AGENTS = [
  'Jett', 'Raze', 'Breach', 'Omen', 'Brimstone',
  'Phoenix', 'Sage', 'Sova', 'Viper', 'Cypher',
  'Reyna', 'Killjoy', 'Skye', 'Yoru', 'Astra',
  'KAY/O', 'Chamber', 'Neon', 'Fade', 'Harbor',
  'Gekko', 'Deadlock', 'Iso'
];

export default function InstantLockerPage() {
  const [isEnabled, setIsEnabled] = useState(false);
  const [selectedAgent, setSelectedAgent] = useState('Jett');
  const [hotkey, setHotkey] = useState('F1');
  const [isSettingHotkey, setIsSettingHotkey] = useState(false);
  const [apiKey, setApiKey] = useState('');

  const handleHotkeySet = () => {
    setIsSettingHotkey(true);
    const handleKeyPress = (e: KeyboardEvent) => {
      e.preventDefault();
      setHotkey(e.key.toUpperCase());
      setIsSettingHotkey(false);
      window.removeEventListener('keydown', handleKeyPress);
    };
    window.addEventListener('keydown', handleKeyPress);
  };

  const handleTestConnection = () => {
    // Implement API connection test
    console.log('Testing API connection...');
  };

  return (
    <div className="container mx-auto p-8">
      <Card className="w-full max-w-2xl mx-auto">
        <CardHeader>
          <CardTitle>Instant Locker Configuration</CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="flex items-center justify-between">
            <Label htmlFor="instalocker-toggle">Enable Instant Locker</Label>
            <Switch
              id="instalocker-toggle"
              checked={isEnabled}
              onCheckedChange={setIsEnabled}
            />
          </div>

          <div className="space-y-4">
            <div className="space-y-2">
              <Label>Preferred Agent</Label>
              <Select value={selectedAgent} onValueChange={setSelectedAgent}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  {AGENTS.map((agent) => (
                    <SelectItem key={agent} value={agent}>
                      {agent}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label>API Key</Label>
              <div className="flex gap-2">
                <Input
                  type="password"
                  value={apiKey}
                  onChange={(e) => setApiKey(e.target.value)}
                  placeholder="Enter your API key"
                />
                <Button onClick={handleTestConnection} variant="outline">
                  Test Connection
                </Button>
              </div>
            </div>

            <div className="space-y-2">
              <Label>Activation Hotkey</Label>
              <div className="flex gap-2">
                <Input value={hotkey} readOnly className="w-32" />
                <Button onClick={handleHotkeySet} variant="outline">
                  {isSettingHotkey ? 'Press any key...' : 'Set Hotkey'}
                </Button>
              </div>
            </div>
          </div>

          <div className="pt-4">
            <p className="text-sm text-zinc-400">
              Status: {isEnabled ? 'Active' : 'Disabled'} - Agent: {selectedAgent}
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
